.sidebar-content {
  box-sizing: border-box;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.sidebar-content .filters {
  flex: 1 1;
  overflow-y: auto;
  padding: 12px 24px;
}
.sidebar-content .btn-groups {
  flex: 0 1 1rem;
  padding: 0.22rem;
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100%;
}
.sidebar-content .btn-groups .am-button {
  width: 3rem;
  font-size: 0.45rem;
  box-sizing: border-box;
  height: 1rem;
  line-height: 1rem;
  border-radius: 0.5rem;
}
.sidebar-content .btn-groups .am-button:before {
  border-radius: 40px;
}
.sidebar-content .areas-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
.sidebar-content .areas-list .am-button {
  margin-right: 5px;
  margin-bottom: 5px;
  flex: 1 0 auto;
  font-size: 0.3rem;
  height: 0.8rem;
  line-height: 0.8rem;
}
.sidebar-content .sub-title {
  color: #888;
  font-size: 0.4rem;
  line-height: 0.8rem;
  margin: 0;
}
.sidebar-content .am-list-line .am-list-content {
  font-size: 0.4rem;
}
.my-drawer {
  position: relative;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.my-drawer .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
