.xui-list {
  width: 100%;
  padding-top: 15px;
}
.xui-list .xui-list-item {
  background: #fff;
  text-align: left;
  padding: 10px 15px 10px;
  margin-bottom: 15px;
}
.xui-list .xui-list-item .item-meta {
  position: relative;
  height: 0.8rem;
  margin-bottom: 0.2rem;
  display: flex;
  align-items: center;
}
.xui-list .xui-list-item .item-meta .icon-id {
  width: 28px;
  height: 28px;
  line-height: 28px;
  position: absolute;
  top: 0;
  background: #1890ff;
  border-radius: 50%;
  color: #fff;
  text-align: center;
  font-size: 13px;
}
.xui-list .xui-list-item .item-meta .meta-title {
  text-indent: 38px;
  font-size: 0.4rem;
  font-weight: bold;
  margin: 0;
}
.xui-list .xui-list-item .item-meta .area {
  height: 0.7rem;
  line-height: 0.7rem;
  min-width: 2rem;
  text-align: center;
  background: #FF9800;
  color: #fff;
  border-radius: 2px;
  font-size: 0.3rem;
  position: absolute;
  right: 30px;
}
.xui-list .xui-list-item .item-meta .location {
  position: absolute;
  right: 0;
}
.xui-list .xui-list-item .item-meta .location img {
  height: 0.5rem;
}
.xui-list .xui-list-item .item-content {
  line-height: 2;
}
.xui-list .xui-list-item .item-content .meta-line {
  display: flex;
  font-size: 0.4rem;
}
.xui-list .xui-list-item .item-content .meta-line > div {
  flex: 1 1;
}
.xui-list .xui-list-item .item-content .meta-line .time {
  flex-basis: 100px;
}
.xui-list .xui-list-item .item-content p > div {
  display: inline;
  margin-right: 30px;
  font-size: 14px;
}
.xui-list .xui-list-item .item-content p.description {
  font-size: 0.38rem;
  margin: 0.2rem 0 0;
}
.xui-list .xui-list-item .item-content .price {
  color: red;
}
.xui-list .xui-list-item.isover .meta-title {
  text-decoration: line-through;
}
@media all and (min-width: 768px) {
  .xui-list {
    background: transparent;
  }
  .xui-list .xui-list-item {
    margin-bottom: 0;
    border-bottom: 1px solid #cfcfcf;
  }
}
